import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Menu from '@material-ui/core/Menu';
import TextField from '@material-ui/core/TextField';
import { fetchParts } from '../store/parts';
import { makeBuild } from '../store/builds';
import { Box, Button, Checkbox, FormControl, FormControlLabel, Grid, InputLabel, MenuItem, Select } from '@material-ui/core';


function CreateBuild() {
    const dispatch = useDispatch();
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    const [caseId, setCaseId] = useState('');
    const [motherBoardId, setMotherBoardId] = useState('');
    const [cpuId, setCpuId] = useState('');
    const [coolerId, setCoolerId] = useState('');
    const [hardDriveId, setHardDriveId] = useState('');
    const [gpuId, setGpuId] = useState('');
    const [ramId, setRamId] = useState('');
    const [powerSupplyId, setPowerSupplyId] = useState('');
    const [networkCardId, setNetworkCardId] = useState('');
    const user_id = useSelector(state => state.auth.user.id);
    const part = useSelector(state => state.partsReducer);
    const [anchorEl, setAnchorEl] = useState('');

    useEffect(() => {
        dispatch(fetchParts())
    }, [dispatch])

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        await dispatch(makeBuild(title, description, user_id, caseId, motherBoardId, cpuId,
            coolerId, hardDriveId, ramId, gpuId, powerSupplyId, networkCardId));
        window.location.href = "/";
    }

    if (part.cases === undefined) {
        return null;
    }

    return (
        <>
            <h1 style={{ textAlign: "center" }}>Create Build</h1>
            <Grid container style={{ justifyContent: "center", marginTop: "5%" }}>
                <Box border={1} style={{ padding: "2%" }}>
                    <form method="POST" action="/api/builds" onSubmit={handleSubmit}>
                        <FormControl>
                            <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
                                Cases
                            </Button>
                            <Menu
                                id="simple-menu"
                                anchorEl={anchorEl}
                                keepMounted
                                open={Boolean(anchorEl)}
                                onClose={handleClose}
                            >
                                {Object.values(part.cases).map(object => {
                                    return (
                                        <MenuItem key={object.id} value={object.id} onClick={handleClose}>{object.name}</MenuItem>
                                    )
                                })}
                            </Menu>
                            <Button type="submit">Create</Button>
                        </FormControl>
                    </form>
                </Box>
            </Grid>
        </>
    );
}

export default CreateBuild;
